@import "src/style";

.landing{
  min-height: 100vh;
  height: auto;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 $horizontal-padding;
  border-bottom: 10px solid rgb(250, 250, 250);
  overflow: hidden;

  h4{
    font-size: 1.5rem;
    font-weight: 500;

    @media all and (max-width: 990px) {
      font-size: 1rem;
    }
  }

  p{
    font-size: 2rem;
    font-weight: 700;
    margin: 3% 0;
    width: 70%;

    @media all and (max-width: 990px) {
      font-size: 1.5rem;
      width: 100%;
    }
  }

  .buttons{
    margin-top: 8%;

    .contactBtn{
      margin-left: 3%;
    }
  }

  img{
    width: 40%;
    object-fit: cover;
    display: inline;

    @media all and (max-width: 990px) {
      display: none;
    }
  }
}