@import "src/style";

.portfolioItem{
  margin: 0.1%;
  background-size: cover;
  background-position: center;
  height: 200px;

  .itemHover{
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(5px);
    color: #FFFFFF;
    padding: 5% 10%;
    cursor: pointer;

    p{
      font-size: 1rem;
      font-weight: bold;
      margin-bottom: 15%;
      width: auto;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .tags{
      display: flex;
      flex-wrap: wrap;
      width: 100%;

      span{
        background: rgba(255, 255, 255, 0.7);
        color: #000000;
        padding: 1% 1.5%;
        font-size: 0.75rem;
        font-weight: bold;
        margin: 1% 1% 0 0;
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
      }
    }
  }
}

.wrapper{
  width: 0;
  height: 0;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99999;

  .detailsBox{
    background: rgba(255, 255, 255, 1);
    position: fixed;
    z-index: 99;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;

    .closeIcon{
      border: 2px solid rgba(0, 0, 0, 0.8);
      color: rgba(0, 0, 0, 0.8);
      width: 40px;
      height: 40px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      position: fixed;
      top: 3%;
      right: 3%;
      cursor: pointer;

      @media all and (max-width: 990px) {
        width: 40px;
        height: 40px;
      }
    }

    img{
      box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
      border-radius: 5px;
      height: 50%;
      object-fit: cover;

      @media all and (max-width: 990px) {
        display: block !important;
        width: 100%;
      }
    }

    .details{
      width: 100%;
      padding: 2%;
      background: rgba(0, 0, 0, 0.8);
      color: #FFFFFF;
      position: absolute;
      bottom: 0;
      left: 0;

      @media all and (max-width: 990px) {
        padding: 4%;
      }

      h2{
        font-weight: 500;
        font-size: 1.5rem;
        margin: 0;
        padding: 0;

        @media all and (max-width: 990px) {
          font-size: 1.2rem;
        }

        span{
          color: #72CAEE;
          cursor: pointer;
          font-size: 1rem;

          @media all and (max-width: 990px) {
            font-size: 0.8rem;
          }
        }
      }

      p{
        font-weight: 300;
        font-size: 1rem;
        margin: 1% 0 0 0;
        padding: 0;

        @media all and (max-width: 990px) {
          font-size: 0.8rem;
          margin: 2% 0 0 0;
        }
      }
    }
  }
}
